import React from 'react';
import Media from 'react-media';
import style from './fourth-screen.module.css';
import Header from './header/header';
import NavigationServices from '../navigationServices/navigationServices';
import Information from './infornation/information';
import imgOffice from '../../img/infoImg/CompanyOffice.png';

const FourthSection = () => (
  <section id="CompanyOffice" className={style.section}>
    <Header />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <div className={style.navigation}>
            <NavigationServices number={3} />
          </div>
        )
      }
    </Media>
    <Information />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && <img className={style.img} alt="Office" src={imgOffice} />
      }
    </Media>
  </section>
);

export default FourthSection;
