import React from 'react';
import classNames from 'classnames';
import style from './navigationServices.module.css';

const arr = [
  { name: '01', href: '#Portal' },
  { name: '02', href: '#Office' },
  { name: '03', href: '#CompanyOffice' },
  { name: '04', href: '#Messages' },
  { name: '05', href: '#Registrator' },
  { name: '06', href: '#' },
  { name: '07', href: '#' },
];
const linkStyle = classNames(style.listItem, style.active);
const numberColorWhite = classNames(style.link, style.linkWhite);
const numberColorBlack = classNames(style.link, style.linkBlack);
const wrapperBlack = classNames(style.wrapper, style.wrapperBlack);
const wrapperWhite = classNames(style.wrapper, style.wrapperWhite);

const Navigation = ({ number }) => (
  <div className={number % 2 === 0 ? wrapperWhite : wrapperBlack}>
    <ul className={style.list}>
      {arr.map((el, i) =>
        number === i + 1 ? (
          <li className={linkStyle} key={el.name}>
            <a
              className={number % 2 === 0 ? numberColorWhite : numberColorBlack}
              href={el.href}
            >
              {el.name}
            </a>
          </li>
        ) : (
          <li className={style.listItem} key={el.name}>
            <a
              className={number % 2 === 0 ? numberColorWhite : numberColorBlack}
              href={el.href}
            >
              {el.name}
            </a>
          </li>
        ),
      )}
    </ul>
  </div>
);

export default Navigation;
